@media screen and (max-width: 428px) {
    .roomDecorate-wrapper{
        font-family: 'League Spartan', sans-serif;
        top: -1px;
       
    }
    .roomDecorate-wrapper *{ margin: 0; padding: 0; text-align: start;}
    .roomDecorate-wrapper h1{ 
        font-weight: 700;
        line-height: 2rem;
    }
    :is(.roomDecorate-wrapper) h2, h3, h4{ 
        font-weight: 700;
        line-height: 1rem;
    }
    :is(.roomDecorate-wrapper) p, button, small{ 
        line-height: 1rem;
        font-size: 12px;
        font-weight: 400;
    }
    :is(.roomDecorate-wrapper) p{ color: hsl(0, 0%, 63%)}
    :is(.roomDecorate-wrapper) button{ 
        cursor: pointer;
        padding: 5px;
    }
    
    .roomDecorate-nav{ display: none; /* new*/ }

    .roomDecorate-nav-hamburger,
    .roomDecorate-nav-hamburger-bgFill{
        display: block;
        position: absolute;
        z-index: 1;
        background-color: transparent;
        min-width: 100%;
        min-height: 80px;
        display: flex;
        align-items: center;
        gap: 15%;
      
    }

    .roomDecorate-nav-hamburger-bgFill{
        background-color: #FFFF;
    }
    .hamburger-logo{
        min-height: 24px;
        display: flex;
        align-items: center;
        padding-left: 5%;
    }
    
    .roomDecorate-nav-left,
    .roomDecorate-nav-right{
        min-height: 16px;
    }
    
    .roomDecorate-nav-left{
        min-width: 74px;
        background-image: url('../../components/Navigate/RoomDecorate/images/logo.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
        position: relative;
        left: 12%;

    }
    .roomDecorate-nav-right{
        min-width: 262px;
        display: flex;
        justify-content: space-between;
        padding-bottom: 5px;     
    }
    
    .roomDecorate-nav-right p{
        color: black; /* changed */
        padding-bottom: 5px;
        position: relative;
        top: 5px;
        
        border-bottom: 2px solid transparent;
        
    }
    .roomDecorate-nav-right p:hover{
        border-bottom: 2px solid black; /* changed */
        cursor: pointer;
    }
    
    .roomDecorate-home{
        min-width: 100%;
        max-height: 100vh; /* changed */
        background-color: #FFFF;
        position: relative;
        top: -1px;
        
        display: grid;

        grid-template-columns: 40% 40% 20%; /* changed */
        grid-template-rows:  repeat(27, 60px) /* changed */
    }
    
    .roomDecorate-home-topLeft,
    .roomDecorate-home-topLeft-PageTwo,
    .roomDecorate-home-topLeft-PageThree{
        grid-column: 1 / 4; /* changed */
        grid-row: 1 / 7; /* changed */
        
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center; /* new */
        
    
    }
    .roomDecorate-home-topLeft{
        background-image: url('../../components/Navigate/RoomDecorate/images/mobile-image-hero-1.jpg'); /* changed */
    }
    .roomDecorate-home-topLeft-PageTwo{
        background-image: url('../../components/Navigate/RoomDecorate/images/mobile-image-hero-2.jpg'); /* changed */
    }
    .roomDecorate-home-topLeft-PageThree{
        background-image: url('../../components/Navigate/RoomDecorate/images/mobile-image-hero-3.jpg'); /* changed */
    }
    
    .roomDecorate-home-topCenter{ 
        grid-column: 3 / 4; /* changed */
        grid-row: 6 / 7; /* changed */
        display: flex;
        justify-content: space-around;
        align-items: flex-end;
    }
    
    .roomDecorate-home-topCenter-prev,
    .roomDecorate-home-topCenter-next{
        max-width: 100%; /* changed */
        min-height: 80%; /* changed */
        color: white;
        border: none;
        background-repeat: no-repeat;
        background-position: center;
        background-color: #000;
        background-size: 20%; /* new */
    }
    :is(.roomDecorate-home-topCenter-prev, .roomDecorate-home-topCenter-next ):hover{
        background-color: #444444;
    }
    
    .roomDecorate-home-topCenter-prev{ background-image: url('../../components/Navigate/RoomDecorate/images/icon-angle-left.svg'); }
    .roomDecorate-home-topCenter-next{ background-image: url('../../components/Navigate/RoomDecorate/images/icon-angle-right.svg'); }
    
    
    .roomDecorate-home-topRight{ 
        grid-column: 1 / 4; /* changed */
        grid-row: 7 / 14; /* changed */
    }
    
    .roomDecorate-home-topRight-textbox{
        position: relative;
        max-width: 80%;
        min-height: 61%;
        left: 50%;
        top: 50%; /* changed */
        transform: translate(-50%, -50%);
    
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    
    }
    .roomDecorate-home-topRight-textbox h1{
        max-width: 90%;
    
    }
    .roomDecorate-home-topRight-textbox p{
        margin: 19px 0;
    }
    .roomDecorate-home-topRight-textbox button{
        display: flex;
        align-items: center;
        gap: 20px;
        max-width: fit-content;
        background-color: transparent;
        border: none;
        font-weight: 600;
        letter-spacing: 0.85rem;
    }
    
    .roomDecorate-home-bottomLeft,
    .roomDecorate-home-bottomRight{
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: top;
    }
    .roomDecorate-home-bottomLeft{ 
        grid-column: 1 / 4; /* changed */
        grid-row: 14 / 18;  /* changed */
    
        background-image: url('../../components/Navigate/RoomDecorate/images/image-about-dark.jpg');
    }
    
    .roomDecorate-home-bottomCenter{
        grid-column: 1 / 4; /* changed */
        grid-row: 18 / 23;     /* changed */
    }
    
    .roomDecorate-home-bottomCenter-textBox{
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        gap: 15px;
        
        max-width: 80%;
        position: relative;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    .roomDecorate-home-bottomCenter-textBox h3{
        font-size: 13px;
        letter-spacing: 0.565em;
    }
    
    
    
    
    .roomDecorate-home-bottomRight{
        grid-column: 1 / 4; /* changed */
        grid-row: 23 / 27;     /* changed */
        
        background-image: url('../../components/Navigate/RoomDecorate/images/image-about-light.jpg');
    }
    
}