.easybank-wrapper{
    position: relative;
    top: -2px;
    padding: 1px;
    background-image: url('../../components/Navigate/Easybank/images/bg-intro-desktop.svg');
    background-repeat: no-repeat;
    background-size: 100%;

    background-position-x: 50vw;
    background-position-y: -12vw;
}
.easybank-wrapper *{margin: 0;}

:is(.easybank-wrapper) button{
    cursor: pointer;
    min-width: 165px;
    max-width: 165px;
    min-height: 45px;
    border-radius: 30px; 
    background: linear-gradient(90deg, #85E8A5 1.21%, #82DAC7 54.23%, #7AD6E3 100%);
    color: #FFF ;
    border: none;
}

:is(.easybank-wrapper) button:hover{ 
    background: linear-gradient(90deg, #2CD262 1.21%, #36BC9B 54.23%, #25B4D3 100%);
}


:is(.easybank-wrapper) p,  small{ color: #9D9CA1; }

:is(.easybank-wrapper) h1, h2, h3{color: #000;}

/* The NavBar */
.easybank-nav-hamburger{ display: none }
.easybank-nav{
    background-color: #FFF;
    position: relative;
    top: 0px;
    left: -1px;
    padding: 1px;
    width: 100%;
    min-height: 82px;

    display: flex;  
    justify-content: space-around;
    align-items: center;
   
    z-index: 1;
}

.easybank-nav-logo{
    min-width: 137px;
    min-height: 23px;
    background-image: url('../../components/Navigate/Easybank/images/logo.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
}

.easybank-nav-links{
    display: flex;
    gap: 30px;
    cursor: pointer;
}


.easybank-nav-links p:hover{
    color: #000;
}


/* header */
.easybank-about{
    background-color: #FFF;
    display: flex;
    height: 738px;
}

.easybank-about-left{
    max-width: 450px;
    max-height: 333px;
    text-align: start;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  
    position: relative;
    top: 130px;
    
    left: 80px;

    letter-spacing: 0.08rem;
    line-height: 1.7rem;
    z-index: 2;
    backdrop-filter: blur(2px);
}

.easybank-about-left h1{
    position: relative;
    margin: 85px 0 30px;
}
.easybank-about-left p{
    margin-bottom: 37px;
}

.easybank-about-right{
    min-width: 615px;
    min-height: 90%;
    position: absolute;
    right: 0;
    top: -3vw;

    background-image: url('../../components/Navigate/Easybank/images/image-mockups.png');
    background-repeat: no-repeat;
}

/* WHY */
.easybank-why{
    background-color: #F4F5F7;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 105px;
}

.easybank-why-top{
    max-width: 585px;
    min-height: 120px;
    text-align: start;

    /* 80px here allso*/
    position: relative;
    left: 80px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    line-height: 1.2rem;
    letter-spacing: 0.02rem;

    margin-bottom: 55px;
}

.easybank-why-bottom{
    width: 85%;
    left: 80px;
    
    /* 80px here allso*/
    position: relative;
    
    text-align: start;
    display: flex;
    
}

.easybank-why-bottom-card{
    max-width: 100%; 
    display: flex;
    min-height: 235px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    margin-right: 10px;
}
.easybank-why-bottom-card:last-child{
    margin-right: 0;
}

.easybank-why-bottom-card-img-1,
.easybank-why-bottom-card-img-2,
.easybank-why-bottom-card-img-3,
.easybank-why-bottom-card-img-4{
    min-width: 75px;
    min-height: 75px;

    /* border-radius: 50%; */
}

.easybank-why-bottom-card-img-1{
    background-image: url('../../components/Navigate/Easybank/images/icon-online.svg');
    background-repeat: no-repeat;
    
}
.easybank-why-bottom-card-img-2{
    background-image: url('../../components/Navigate/Easybank/images/icon-budgeting.svg');
    background-size: 100%;
    background-repeat: no-repeat;
    
}
.easybank-why-bottom-card-img-3{
    background-image: url('../../components/Navigate/Easybank/images/icon-onboarding.svg');
    background-repeat: no-repeat;
    
}
.easybank-why-bottom-card-img-4{
    background-image: url('../../components/Navigate/Easybank/images/icon-api.svg');
    background-repeat: no-repeat;
    
}

/* Article */

.easybank-article{
    background-color: #FFF;
    min-height: 650px;
    padding-bottom: 100px;
}
.easybank-article-top{
    position: relative;
    max-width: 80%;
    left: 80px;
    /* top: 92px; */
    padding-top: 92px;
    margin-bottom: 55px;
    text-align: start;
    
}



.easybank-article-bottom{
    max-width: 80%;
    left: 80px;
    
    /* 80px here allso*/
    position: relative;

    text-align: start;
    display: flex;
}

.easybank-article-bottom-card{
    max-width: 100%; 
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}
.easybank-article-bottom-card:last-child{
    margin-right: 0;
}



.easybank-article-bottom-card-img-1,
.easybank-article-bottom-card-img-2,
.easybank-article-bottom-card-img-3,
.easybank-article-bottom-card-img-4{
    min-width: 100%;
    min-height: 205px;
    border-radius: 5px 5px 0 0;
}

.easybank-article-bottom-card-img-1{
    background-image: url('../../components/Navigate/Easybank/images/image-currency.jpg');
    background-repeat: no-repeat;
    background-size:cover;
    background-position: right;
}
.easybank-article-bottom-card-img-2{
    background-image: url('../../components/Navigate/Easybank/images/image-restaurant.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}
.easybank-article-bottom-card-img-3{
    background-image: url('../../components/Navigate/Easybank/images/image-plane.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}
.easybank-article-bottom-card-img-4{
    background-image: url('../../components/Navigate/Easybank/images/image-confetti.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}

.easybank-article-bottom-crad-text{
    margin-top: 25px;
    min-height: 210px;
  
    max-width: 80%;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    position: relative;
    left: 25px;
    
}

/* Footer */

.easybank-article-footer{
    min-height: 195px;
    background-color: #2D314E;
    display: flex;
    max-width: 100%;
    
}

.easybank-article-footer-left{
    
    min-width: 158px;
    max-height: 110px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    left: 80px;
    top: 48px;
    
}

.easybank-article-footer-left-icons{
    
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.easybank-nav-logo-white{
    
    min-height: 21px;
    background-image: url('../../components/Navigate/Easybank/images/whitelogo.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;   
}
.easybank-article-footer-center-wrapper{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    
    position: relative;
    top: 48px;
    max-height: 112px;
    min-width: 290px;
       
    margin-left: 205px;
    margin-right: 130px;
}

.easybank-article-footer-center{
    
    min-width: 92px;
    text-align: start;
    min-height: 110px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;  
}

.easybank-article-footer-center p{
    cursor: pointer;
}
.easybank-article-footer-center p:hover{
    color: #67A5A4;
}


.easybank-article-footer-right{
    min-width: 225px;
    max-height: 110px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    
    position: relative;
    top: 48px;
}
.easybank-article-footer-right small{
    letter-spacing: 0.05rem;
}