.flag{
    position: relative;
    /* min-height: 408px; */
    display: flex;

    
}
.countries-app-wrapper-dark{
    padding-bottom: 24px;
    /* padding-bottom: 50vh; */
    background-color: #202D36;
}
.countries-app-wrapper-white{
    padding-bottom: 24px;
    background-color: #FFF;
}
.countries-app-wrapper-dark *, .countries-app-wrapper-white *{
    margin: 0;
    padding: 0;
    color: #FFF;
    text-align: start;
    box-sizing: border-box;
   
    /* border: 1px solid red; */
}
.countries-app-wrapper-white *{
    color: #000;
}
.countries-app-wrapper-dark *{
    color: #FFF;
}
:is(.countries-app-wrapper-dark, .countries-app-wrapper-white) p{
    font-size: 14px;
}

:is(.countries-search-right-dropdown, .countries-search-right, .countries-card-box){
    cursor: pointer;
}




/* Flex short curt */
.this-div-is-center{
    position: relative;
    left: 50%;
    transform: translate(-50%)
}

/* Navbar */
.countries-nav-wrapper{
    background-color: #2B3743;
    padding: 1px 10%;
    min-width: 100%;
    min-height: 75px;

    display: flex;
    justify-content: space-between;
    align-items: center;   
}


.countries-nav-left h3{
    font-size: 26px;
}
.countries-nav-right{
    display: flex;
    justify-content: space-between;
    min-width: 125px;
}
.countries-nav-right p{
   min-width: 100px;
}

/* darkMode / whiteMode icon */
.icon-theme-dark{
    background-image: url('../../components/Navigate/Countries/design/darkMode-Dark.png');
}
.icon-theme-white{
    background-image: url('../../components/Navigate/Countries/design/darkMode-white.png');
}
.icon-theme-dark,
.icon-theme-white{
    min-width: 16px;
    max-height: 24px;
    background-position: center;
}


/* Search section */
.countries-search-wrapper{
    max-width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
   
    margin-top: 77px;
    margin-bottom: 47px; /* to change where the top card section will start */
    z-index: 2;
}

.countries-search-left{
    min-width: 41.5%;
    
}
.countries-search-left input{
    min-width: 100%;
    min-height: 60px;
    text-indent: 10px;
}
.countries-search-left input:focus{
    background-color: #2B3743;
}

.countries-search-right{
    min-width: 17.5%; /* 200px */
  

    border-radius: 5px;
    background-color: #2B3743;

    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}



.countries-search-right-dropdown{
    position:absolute;
    right: 0;
    top: 65px;
    min-width: 17.5%; /* 200px */
    display: flex;
    flex-direction: column;
    justify-content: space-around; 
    background-color: #2B3743;
}

.countries-search-right-dropdown ul {
    list-style-type: none;

    border-radius: 5px;
}
.countries-search-right-dropdown li{ padding: 20px; }

.countries-search-right-dropdown li:hover,
.countries-search-right:hover,
.card-clicked-goBack-btn button:hover,
.card-clicked-info-context-bottom button:hover{ 
    cursor: pointer;
    background-color: #394958;
}

/* Card section */

.countries-card-wrapper{
    max-width: 80%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    border: 1px solid red;
    gap: 70px;


   
}

.countries-card-box{
    max-width: 100%;
    /* min-height: 335px; */
    border: 1px solid black;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 25px;
    background-color: #2B3743;
}

.countries-card-img > img{
    min-width: 100%;
    max-height: 160px;
}

.countries-card-context{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 0 25px 50px;
 
}

.countries-card-context h2{
    margin-bottom: 24px;
    font-size: 18px;
}

.countries-card-description{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;
}

.countries-card-description span{
    font-weight: 900;
 
}

/* Card Clicked */

.card-clicked-wrapper{
    min-width: 100%;
    padding-bottom: 50vh;
    z-index: 3;
    background-color: blue;
    position: absolute;
    top: 145px;
    left: 0;
}

.card-clicked-goBack-btn{
    
    padding: 80px 5%; 
}
.card-clicked-goBack-btn button{
    min-width: 10%;
    min-height: 60px;
    background-color: #2B3743;
    border-radius: 5px;
    text-align: center;
    color: white;
    background-image: url('../../components/Navigate/Bookmark/images/icon-arrow.svg');
    background-repeat: no-repeat;
    background-position-x: 18px;
    background-position-y: 22px;
}

.card-clicked-info-wrapper{
    background-color: red;
    max-width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card-clicked-info-img{
    min-width: 40%;
    min-height: 408px;
    background-color: green;
}

.card-clicked-info-container{
    min-width: 40%;
    display: flex;
    flex-direction: column;
  
    align-items: flex-start;
 
  
}

.card-clicked-info-context{
    display: flex;
    min-width: 100%;
    

    padding: 45px 0;
 
}
.card-clicked-info-context span{
    font-weight: 900;
}

.card-clicked-info-context-left,
.card-clicked-info-context-right{
 
    min-width: 50%;
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.card-clicked-info-context-bottom{
    display: flex;
    min-width: 100%;
    align-items: flex-end;
   
    gap: 10px;
}
.card-clicked-info-context-bottom p{
    margin-right: 7px;
}
.card-clicked-info-context-bottom button{
    min-width: 95px;
    min-height: 26px;
    border-radius: 5px;
    background-color: #2B3743;
    text-align: center;
}








