
@media screen and (max-width: 428px) {
    /* the id root is displaying everything from index.js */
    #root{
        max-height: 0px;
    }

    .easybank-wrapper{
        position: relative;
        top: -62px;
        padding: 1px;
        padding-bottom: 0px; /* new */
        background-image: url('../../components/Navigate/Easybank/images/bg-intro-mobile.svg');
        background-repeat: no-repeat;
        background-size: 100%;
    
        background-position-x: center; /* changed */
        background-position-y: 3vw;
        background-color: white;
    }

    .easybank-wrapper *{margin: 0}

    :is(.easybank-wrapper) button{
        cursor: pointer;
        min-width: 165px;
        max-width: 165px;
        min-height: 45px;
        border-radius: 30px; 
        background: linear-gradient(90deg, #85E8A5 1.21%, #82DAC7 54.23%, #7AD6E3 100%);
        color: #FFF ;
        border: none;
    }

    :is(.easybank-wrapper) button:hover{ 
        background: linear-gradient(90deg, #2CD262 1.21%, #36BC9B 54.23%, #25B4D3 100%);
    }
    
    :is(.easybank-wrapper) p,  small{ color: #9D9CA1; }
    
    :is(.easybank-wrapper) h1, h2, h3{color: #000;}
    :is(.easybank-wrapper)  h2{ /* new */ font-size: 28px; /* new */ }
    :is(.easybank-wrapper)  h1{ /* new */ font-size: 28px; /* new */ }
    /* The NavBar */
    .easybank-nav{ display: none; }
    .easybank-nav-links{ display: none;}
    .easybank-nav-btn{ display: none;}
    .easybank-nav-logo{ 
        min-height: 30px;
        min-width: 155px;
        background-size: 100%;
    }
    /**/
    .easybank-nav-hamburger{
        display: block;
        min-width: 100%;
        min-height: 75px;
       
        display: flex;
        justify-content: space-around;
        align-items: center;

        position: relative;
        top: 60px;
    }
    .easybank-nav-hamburger-open,
    .easybank-nav-hamburger-close{
        min-width: 30px;
        min-height: 30px;
     
        background-position: center;
        background-repeat: no-repeat;
        background-size: 100%;
    }

    .easybank-nav-hamburger-open{
        background-image: url('../../components/Navigate/Easybank/images/icon-hamburger.svg');
    }
    .easybank-nav-hamburger-close{
        background-image: url('../../components/Navigate/Easybank/images/icon-close.svg');
    }

    .easybank-nav-hamburger-links{
        position:absolute;
        min-width: 100%;
        min-height: 110vh;
        background: linear-gradient(180deg, #2c324cfc 0%, #f7f7f700 100%);
        
        top: 100%;
       
    }

    .easybank-nav-hamburger-links p{
        font-size: 18px;
        color: #000;
        max-width: 90%;
        min-height: fit-content;
     
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        margin-bottom: -1px;
        z-index: 3;
        display: flex;
        flex-direction: column;
  
        
        background-color: #FFF;
        /* border-radius: 5px; */
        text-align: center;
        padding: 20px 0;
        
        
    }
    .easybank-nav-hamburger-links p:first-child{
        margin-top: 25px;
        border-radius: 5px 5px 0 0;
        padding: 40px 0 0;
     
    }
    .easybank-nav-hamburger-links p:nth-child(3){
        padding: 0;
    }
    .easybank-nav-hamburger-links p:last-child{
        border-radius: 0 0 5px 5px;
        padding: 0 0 40px;

    }
    
    /* header */
    .easybank-about{
        background-color: transparent;
       
        min-height: 690px; /* changed */  
        background-color: transparent;
    }
    
    .easybank-about-left{
        max-width: 80%;
        max-height: 298px; /* changed */
