        flex-direction: column;
        align-items: center; /* changed */
      
        position: relative;
        top: 91vw; /* changed */
        
        
        /* all section are 80px from left */
        left: 50%; /* changed */
        transform: translateX(-50%); /* new */

        letter-spacing: normal; /* new */
        line-height: normal; /* new */
        z-index: 0;
        backdrop-filter: blur(0); /* changed */
    }
    
  
    
    .easybank-about-left h1{
