.shortly-hamburger{display: none;}
/* -------------------------------------------------- Wrapper */
.shortly-wrapper{
    position: relative;
    background-color: #FFF;
    top: -1px;
}
.shortly-wrapper *{
    margin: 0;
    text-align: start;
}
:is(.shortly-wrapper) button{
    border: none;
    border-radius: 20px;
    color: #FFF;
    padding: 0.3rem;
    min-width: 100px;
    text-align: center;
    cursor: pointer;
}
:is(.shortly-wrapper) button:hover{
    color: #FFFF;
    background-color: #9BE3E2;
}

:is(.shortly-wrapper) p{ color: #9A999F; }

/* NAV */
.shortly-nav *{ 
    font-weight: 600;  
    cursor: pointer; 
    color: #97969E;
}

.shortly-nav{

    min-height: 5rem;

    display: flex;
    justify-content: space-around;
    align-items: center;
}

.shortly-nav-left{
    display: flex;
    align-items: center;
    gap: 2.5rem;
}

.shortly-nav-left p:hover{ color: #000; }

.shortly-nav-right{
    display: flex;
    gap: 0.2rem;
}

.shortly-nav-right button:nth-child(1){
    color: #97969E;
    background-color: transparent;
}
.shortly-nav-right button:nth-child(1):hover{
    background-color: #9BE3E2;
    color: #FFF;
}

.shortly-nav-right button:nth-child(2){ 
    background-color: #29D2CF; 
}

.shortly-nav-right button:nth-child(2):hover{ 
    background-color: #9BE3E2; 
}

/* --------------------------------------------------  About */
.shortly-about{
    position: relative;
    max-width: 90%;
    max-height: 25%;
    left: 10%;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.shortly-about-left{
    max-width: 50%;
    max-height: 20%;

    display: flex;
    flex-direction: column;
    gap: 20px;
}

.shortly-about-left h1{
    font-size: 60px;
    font-weight: 900;
}

.shortly-about-left p{
    max-width: 90%;
    font-size: 20px;
}

.shortly-about-left button{
    max-width: 195px;
    min-height: 53px;
    border-radius: 50px;
 
    background-color: #29D2CF;
    
    margin-top: 20px;
    font-weight: 700;
    font-size: 20px;
    cursor: pointer;
}

.shortly-about-right{
 background-color: transparent;
 min-width: 50%;
 min-height: 450px;

 background-image: url('../../components/Navigate/Shortly/images/illustration-working.svg');
 background-position: center;
 background-repeat: no-repeat;
 background-size: 100%;
}

/* --------------------------------------------------  Search */
.shortly-search{
    background-color: #F0F1F6;
    position: relative;
    top: 95px;

}
.shortly-search-top{
    max-width: 80%;
    min-height: 115px;

    display: flex;
    align-items: center;
    justify-content: space-evenly;
    position: relative;

    left: 50%;
    top: -50px;
    transform: translateX(-50%);
    margin: 2rem 0;

    background-image: url('../../components/Navigate/Shortly/images/bg-shorten-desktop.svg');
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;

    background-color: #3A3053;
    border-radius: 5px;

}

.shortly-search-top input{
    min-width: 80%;
    min-height: 30px;
    border-radius: 5px;
    text-indent: 10px;
    text-transform: none;
    outline: none;
}

.shortly-search-top input:focus{ background-color: white; }

.shotly-input-error{
    background-image: url('../../components/Navigate/Bookmark/images/icon-error.svg');
    background-repeat: no-repeat;
    background-position:calc(98%)
}

.shotly-input-good{
    background-image: url('../../components/Navigate/Bookmark/images/favicon-32x32.png');
    background-repeat: no-repeat;
    background-position:calc(98%)
}

.shortly-search-top button{
    background-color: #29D2CF;
    border-radius: 5px;
    min-height: 35px;
    cursor: pointer;
    font-weight: 900;
}

.shortly-search-top small{
    position:absolute;
    top: 80px;
    left: 3%;

    color: #E7676B;
    text-shadow: -1px 1px #000;
    font-weight: 700;
    letter-spacing: 0.08rem;
}

.shortly-search-bottom{
    display: flex;
    flex-direction: column;
    gap: 5px;

    background-color: #F0F1F6;
    padding-bottom: 10px;
}
/* --------------------------------------------------  Search Added Links */
.mappedLinks{
    display: flex;
    justify-content: space-between;
    align-items: center;

    min-height: 70px;
    max-width: 80%;

    position: relative;
    left: 50%;
    top: -50px;
    transform: translateX(-50%);
    border-radius: 5px;
    background-color: #FFFF;
}

.mappedLinks-right{
    display: flex;
    align-items: center;
    gap: 15px;
    
    position: relative;
    right: 15px
}

.mappedLinks p{
    position: relative;
    text-indent: 10px;
    color: #000;
}

.mappedLinks > .mappedLinks-right p{
    color: #55CEC7;
    font-weight: 600;
}

.mappedLinks button{
    background-color: #29D2CF;
    border-radius: 5px;

    font-weight: 900;
    cursor: pointer;
    
    min-width: 105px;
    min-height: 40px;
}

.shortenRemoveLink{
    position: absolute;
    right: -25px;    

    fill:#29D2CF;
}

.shortenRemoveLink:hover{ fill: #E7676B; }

/* --------------------------------------------------  Statistics */
.shortly-statistics{
    position: relative;
    top: 59px;
    padding: 1px;
    background-color: #F0F1F6;
    }

    .shortly-statistics-top{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        max-width: 30%;
        min-height: 100px;

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;

        margin-top: 110px;
        margin-bottom: 90px;
    }
    
    .shortly-statistics-top p{
        text-align: center;
    }
    
    .shortly-statistics-bottom{
        position: relative;
        transform: translateX(-50%);
        left: 50%;
        max-width: 80%;
        min-height: 335px;

        display: flex;
        justify-content: space-between;

        margin-bottom: 16%;
    }   
    
    .shortly-statistics-bottom-card{
        position: relative;
        max-width: 28%;
        max-height: 10vw;
        background-color: #FFFF;
        border-radius: 5px;
        padding: 70px 15px 35px;
        
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    
    
    .shortly-statistics-bottom-card:nth-child(3){ top: 35px; }
    .shortly-statistics-bottom-card:nth-child(4){ top: 70px; }

    .shortly-statistics-bottom-card h3{ padding-bottom: 20px; }
    .shortly-statistics-bottom-card p{ color: #A09FA4; }

    .shortly-statistics-card-image{
        position: absolute;
        top: -35px;
        left: 35px;
    
        background-color: #000;
        padding: 10px;
        border-radius: 50%;
    }

    .shortly-statistics-bottom-hr{
        position: absolute;
        top: 41%;
        background-color: aqua;

        min-height: 8px;
        min-width: 100%;
    }

    /* --------------------------------------------------  Footer */
    .shortly-footer{ position: relative; }

    .shortly-footer-top{
        background-color: #3A3053;
        background-image: url('../../components/Navigate/Shortly/images/bg-boost-desktop.svg');
        background-repeat: no-repeat;
        background-size: cover;

        min-height: 260px;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
    }

    .shortly-footer-top button{
        min-width: 17%;
        min-height: 60px;

        background-color: #29D2CF;
        
        font-size: larger;
        font-weight: 900;
        border-radius: 30px;
    }
    
    .shortly-footer-top h2{
        color: #FFFF;
        font-size: 40px;
    }
    
    .shortly-footer-bottom{
        background-color: #232027;
        color:#FFFF;

        display: flex;
        justify-content: space-around;
        align-items: flex-start;

        min-height: 210px;
        padding-top: 4.5rem;
    }

    :is(.shortly-facebook, .shortly-instagram, .shortly-twitter, .shortly-pinterest){ fill: #FFF; }
    :is(.shortly-facebook, .shortly-instagram, .shortly-twitter, .shortly-pinterest):hover{ fill: #29D2CF; }

    .shortly-footer-center{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        
        min-height: 165px;
        min-width: 466px;
    }

    .shortly-footer-center h4{ margin-bottom: 27px; }
    .shortly-footer-center p{ margin-bottom: 1rem; }

    .shortly-footer-right{
        min-width: 175px;
        min-height: 36px;

        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    :is(.shortly-footer-bottom) p:hover{
        color: #29D2CF;
        cursor: pointer;
    }