html{
  margin: 0;
  text-align: center;
  scroll-behavior: smooth;  
 /*  font-size: calc(10px + 2vmin); */
  background-color: red;

  background: url(../components/images/background-dektop1.png);
  background-repeat: no-repeat;
  background-size: 100%;

}

a{
  color: #252525;
}
.startWrapper{
  background-repeat: no-repeat;
  background-size:cover;

  position: relative;
  top: -1px;
  padding: 1px;
}
.home-bg {
  color: #D9D9D9;
}
.homeLinks{
  width: 200px;
 
  background-color: #536050;
  padding: 30px;
  border-radius: 20% 70% 30% 80%;
  box-shadow: -5px 10px 30px white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  position: relative;
  left: 50%;
  transform: translateX(-50%);
  

}
.homeLinks a{
  color: rgb(227, 104, 52);
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  margin-bottom: 10px;
}
.homeLinks a:last-child{
  padding-bottom: 35px;
}
.homeLinks a:hover{
  transform: scale(110%);
  text-shadow: -1px 2px;
  color:white;
}

/* .App {
  background-color: purple;
  height: 100vh;
  min-width: 1380px;
  font-size: calc(10px + 2vmin);
  color: white;
} */



.nav-logo{
  width: 50vw;
  min-height: 60px;
  

  background-image: url('../components/Navigate/Home/background/prodject-img/atle-logo.png');
  background-repeat: no-repeat;
}
.nav-logo:hover{
  background-image: url('../components/Navigate/Home/background/prodject-img/logo-back-home.png');
  background-position-y: 9px;
  background-position-x: 20px;
}
.home-bg h1{
  text-shadow: -1px 1px  black;
}
.nav{
  background: linear-gradient(90deg, #D50059 -101.81%, #144B53 99.98%, #01C4CD 100%);
  padding: 8px;
  position: relative;
  top: -1px;
  border-bottom: 2px solid #252525;
  /* min-width: 1380px; */
  z-index: 5;
}
.home-prodject-wrapper{
  max-width: 90%;
  margin: 40px auto;
  display: flex;
  gap: 30px;
  justify-content: center;
  align-items: center;

  background-color: transparent;
  border-radius: 20px;
  
}
.btn-prev{
  background-image: url('../components/images/btn-arrow.png');
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: center;
}
.btn-prev:hover{
  background-image: url('../components/images/btn-arrow-prev.png');
  
}
.btn-next{
  background-image: url('../components/images/btn-arrow.png');
  background-repeat: no-repeat;
  background-size: 50%;
  transform: rotate(180deg);
  background-position: center;
}
.btn-next:hover{
  background-image: url('../components/images/btn-arrow-next.png');
  transform: rotate(0deg);
}

.home-prodject-wrapper button{
  background-color: transparent;
  border: 1px solid #D9D9D9;
}
.home-prodject-wrapper button:hover,
.home-prodject-wrapper button:first-child:hover{
  filter: drop-shadow(0px 0px 5px #FFFBFB);
  background-color: #D9D9D9
}

.home-prodject-container{
  border: 1px solid #D9D9D9;
  border-radius: 20px;
 
  display: flex;
  align-items: center;
  width: 80%;

  background-color: transparent;


}

.prodject-card{
  min-width: 50%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;

}
.prodject-card h5{
  color: tomato;

}

.prodject-card a{
  color: white;
}
.home-prodject-wrapper button{
  min-height: 100px;
  min-width: 100px;
  border-radius: 50%;
  cursor: pointer;

}
.prodject-card div{
  width: 50vh;

  height: 30vh;
  cursor: pointer;
  
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  
  filter: drop-shadow(0px 0px 2px #FFFBFB);
}
.img1{ background-image: url('../components/Navigate/Home/background/prodject-img/img1.png'); }
.img2{ background-image: url('../components/Navigate/Home/background/prodject-img/img2.png'); }
.img3{ background-image: url('../components/Navigate/Home/background/prodject-img/img3.png');
   width: 45vh !important;
}
.img4{ background-image: url('../components/Navigate/Home/background/prodject-img/img4.png'); }
.img5{ background-image: url('../components/Navigate/Home/background/prodject-img/img5.png'); }
.img6{ background-image: url('../components/Navigate/Home/background/prodject-img/img6.png'); }
.img7{ background-image: url('../components/Navigate/Home/background/prodject-img/img7.png'); }
.img8{ background-image: url('../components/Navigate/Home/background/prodject-img/img8.png'); }
.img9{ background-image: url('../components/Navigate/Home/background/prodject-img/img9.png'); }
.img10{ background-image: url('../components/Navigate/Home/background/prodject-img/img10.png'); }
.img11{ background-image: url('../components/Navigate/Home/background/prodject-img/img11.png'); }


:is(.home-prodject-container *):hover{ filter: drop-shadow(0px 0px 5px #FFFBFB); }

/* Error Page */
.pageNotFound{
  background-color: red;
  height: 100vh;
  position: relative;
  top: -80px;
  text-align: center;
}

@media screen and (max-width: 900px) {
  html{
 
  
    background: url(../components/Navigate/Home/background/prodject-img/background-img-mobile.png);
    background-repeat: repeat-y;
    background-size: 100%;
    background-position: top;
    
    background-color: #1D1D1D;
    
  }
  
  .home-bg h1{
  font-size: 1.2rem;
  }
  .home-bg p,
  .prodject-card h5{
    font-size: 0.6rem;
  }
  .startWrapper{
    background-repeat: no-repeat;
    background-size:cover;
  
    position: relative;
    top: -1px;
    padding: 1px;
  }
  
  .home-bg h1,
  .prodject-card h5,
  .spanColor{
    color: lightgray;
    text-shadow: -0.5px 1.5px tomato;
    letter-spacing: 0.09rem;
  }
 
  .home-bg p{
    color: #000;
    font-weight: 900;
    border: 1px solid tomato;
    background-color: #666;
    width: fit-content;
    margin: 0 auto;
    padding: 0.5rem 1rem;
    border-radius: 20px;
  }


  .nav-logo{
    background-size: 80%;
    background-position-y: 10px ;
    background-position: top;
    width: 100px;
    min-height: 35px;
  }
 

.btn-prev{ background-size: 20%; }

  .btn-next{ background-size: 20%; }
  
  .home-prodject-container{
    flex-direction: column;
    align-items: center;

   border: transparent;
    
    max-width: 40%;

   
  }


  .home-prodject-wrapper{
    width: 90%;
    justify-content: space-evenly;
 
  }

  .home-prodject-wrapper button{
    min-height: 45px;
    min-width: 45px;
    border-radius: 50%;
    cursor: pointer;
    z-index: 2;
  
  }
  .prodject-card div{
    width: 30vh;
  
    height: 30vh;
    cursor: pointer;
    
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    
    filter: drop-shadow(0px 0px 2px #FFFBFB);
    border-radius: 50%;
    border: 2px solid transparent;
    box-shadow: 1px 1px 20px white;
  }

  .img3{ background-image: url('../components/Navigate/Home/background/prodject-img/img3.png');
     width: 30vh !important;
  }  
}