@media screen and (max-width: 428px) {
    /* -------------------------------------------------- Wrapper */
.shortly-wrapper{
    position: relative;
    top: -1px;
}
.shortly-wrapper *{
    margin: 0;
    text-align: start;
}
:is(.shortly-wrapper) button{
    border: none;
    border-radius: 20px;
    color: #FFF;
    padding: 0.3rem;
    min-width: 100px;
    text-align: center;
    cursor: pointer;
}
:is(.shortly-wrapper) button:hover{
    color: #FFFF;
    background-color: #9BE3E2;
}

:is(.shortly-wrapper) p{ color: #9A999F; }

/* NAV */

.shortly-hamburger{
    display: block; /* changed */
  
    position: absolute;
    top: 0%;
    z-index: 2;
    min-height: 30px;
    min-width: 80%;
    position: relative;
    left:50%;
    transform: translateX(-50%);
    display: flex;
    padding: 1rem;
    align-items: center;
    justify-content: space-between;
    background-color: transparent;
}
.shortly-nav *{ 
    font-weight: 600;  
    cursor: pointer; 
    color: #97969E;
}

.shortly-nav{
    background-color: transparent;
    min-height: 5rem;
    
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column; /* new */
    max-width: 90%; /* new */
    padding: 2.5rem 0 2.5rem ;
    border-radius: 10px;/* new */
   
    
    background-color: #3A3053;/* new */
    position: relative;/* new */
    top: 3.5%;/* new */
    left: 50%;/* new */
    transform: translateX(-50%);/* new */
    z-index: 1;/* new */
    

}
.shortly-nav *{font-weight: 900;} /* new */

.shortly-nav-left{
    display: flex;
    flex-direction: column; /* new */
    align-items: center;
    gap: 2.5rem;
    border: none;
    border-bottom: 1px solid #544A6D;
    min-width: 90%; /* new */
    padding-bottom: 1.2rem; /* new */
    margin-bottom: 0.60rem; /* new */
    
}
.shortly-nav-left svg{ /* new */
   display: none;
}

.shortly-nav-left p{ /* new */
    color: #FFF; /* new */
}
.shortly-nav-left p:hover{ 
    color: #000; }

.shortly-nav-right{
    display: flex;
    gap: 0.2rem; 
    flex-direction: column; /* new */

    min-width: 90%; /* new */
}

.shortly-nav-right button:nth-child(1){
    color: #FFF; /* changed */
    background-color: transparent;
    min-height: 50px; /* new */
    border-radius: 50px; /* new */
}

.shortly-nav-right button:nth-child(1):hover{
    background-color: #9BE3E2;
    color: #FFF;
}

.shortly-nav-right button:nth-child(2){ 
    background-color: #29D2CF; 
    min-height: 50px; /* new */
    border-radius: 50px; /* new */
}

.shortly-nav-right button:nth-child(2):hover{ 
    background-color: #9BE3E2; 
}

/* --------------------------------------------------  About */
.shortly-about{
    position: relative;
    max-width: 90%;
    max-height: 25%;
    left: 50%; /* changed*/
    transform: translateX(-50%); /* new */
  

    display: flex;
    flex-direction: column-reverse; /* new */
    justify-content: space-between;
    align-items: center;
    
}

.shortly-about-left{
    max-width: 90%; /* changed */
    max-height: 20%;

    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    
}


.shortly-about-left h1{
    max-width: 85%; /* new */
    font-size: 40px; /* changed */
    font-weight: 900;
    text-align: center; /* new */
}

.shortly-about-left p{
    max-width: 90%;
    font-size: 20px;
    text-align: center; /* new */

}

.shortly-about-left button{
    min-width: 195px; /* changed */
    min-height: 53px;
    border-radius: 50px;
 
    background-color: #29D2CF;
    
    margin-top: 20px;
    font-weight: 700;
    font-size: 20px;
    cursor: pointer;
}

.shortly-about-right{
 background-color: transparent;
 min-width: 100%;
 max-height: 340px; /* new */
  
 background-image: url('../../components/Navigate/Shortly/images/illustration-working.svg');
 background-position: left; /* changed */
 background-repeat: no-repeat;
 background-size: 160%; /* changed */
}

/* --------------------------------------------------  Search */
.shortly-search{
    background-color: #F0F1F6;
    position: relative;
    top: 95px;

}
.shortly-search-top{
    max-width: 90%; /* changed */
    min-height: 185px; /* changed */

    display: flex;
    flex-direction: column; /* new */
    align-items: center;
    justify-content: space-evenly;
    position: relative;

    left: 50%;
    top: -50px;
    transform: translateX(-50%);
    margin: 2rem 0;

    background-image: url('../../components/Navigate/Shortly/images/bg-shorten-mobile.svg'); /* changed */
    background-repeat: no-repeat;
    background-size: 100%; 
    background-position-x: 70px; /* changed */
    background-position-y: -70px; /* changed */

    background-color: #3A3053;
    border-radius: 5px;

}

.shortly-search-top input{
    min-width: 80%;
    min-height: 50px; /* changed */
    border-radius: 5px;
    text-indent: 10px;
    text-transform: none;
    outline: none;
}

.shortly-search-top input:focus{ background-color: white; }

.shotly-input-error{
    background-image: url('../../components/Navigate/Bookmark/images/icon-error.svg');
    background-repeat: no-repeat;
    background-position:calc(98%)
}

.shotly-input-good{
    background-image: url('../../components/Navigate/Bookmark/images/favicon-32x32.png');
    background-repeat: no-repeat;
    background-position:calc(98%)
}

.shortly-search-top button{
    background-color: #29D2CF;
    border-radius: 5px;
    min-height: 50px; /* changed */
    min-width: 80%; /* new */
    cursor: pointer;
    font-weight: 900;
    font-size: 20px; /* new */
}

.shortly-search-top small{
    position:absolute;
    top: 86px; /* changed */
    left: 10%; /* changed */

    color: #E7676B;
    text-shadow: -1px 1px #000;
    font-weight: 700;
    letter-spacing: 0.08rem;
}

.shortly-search-bottom{
    display: flex;
    flex-direction: column;
    gap: 5px;

    background-color: #F0F1F6;
    padding-bottom: 10px;
}
/* --------------------------------------------------  Search Added Links */
.mappedLinks{
    display: flex;
    flex-direction: column; /* new */
    justify-content: space-around;
    align-items: flex-start; /* changed */

    min-height: 70px;
    max-width: 90%;
    box-sizing: border-box;
    padding: 10px;

    position: relative;
    left: 50%;
    top: -50px;
    transform: translateX(-50%);
    border-radius: 5px;
    background-color: #FFFF;
}

.mappedLinks-right{
    display: flex;
    flex-direction: column; /* new*/
    align-items: flex-start; /* changed */
    gap: 15px;
    
    position: relative;
    right: 15px;
    left: 0; /* new */
    border: none;
    border-top: 3px solid #F0F1F6;
    min-width: 100%; /* new */
    margin-top: 5px;
    padding-top: 5px;
  
}

.mappedLinks p{
    position: relative;
    text-indent: 0px; /* changed */
    color: #000;
}

.mappedLinks > .mappedLinks-right p{
    color: #55CEC7;
    font-weight: 600;
}

.mappedLinks button{
    background-color: #29D2CF;
    border-radius: 5px;

    font-weight: 900;
    cursor: pointer;
    
    min-width: 100%;
    min-height: 40px;
}

.shortenRemoveLink{
    position: absolute;
    right: 20px; /* changed */
    top: 8px;    /* changed */

    fill:#29D2CF;
}

.shortenRemoveLink:hover{ fill: #E7676B; }

/* --------------------------------------------------  Statistics */
.shortly-statistics{
    position: relative;
    top: 59px;
    padding: 1px; 
  
    max-width: 90%; /* new */
    left: 50%; /* new */
    transform: translateX(-50%); /* new */
    background-color: #F0F1F6;

    }

    .shortly-statistics-top{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        
        min-width: 90%; /* new */
        min-height: 100px;
      
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;

        margin-top: 110px;
        margin-bottom: 90px;
    }
    
    .shortly-statistics-top p{
        text-align: center;
        max-width: 85%; /* new */
        letter-spacing: 0.05rem; /* new */
    }
    
    .shortly-statistics-bottom{
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        
        min-width: 90%; /* new */
        min-height: 335px;
   

        display: flex;
        flex-direction: column;/* new */
        justify-content: space-between;
        align-items: center; /* new */
        gap: 50px;/* new */
        margin-bottom: 16%;
    }   
    
    .shortly-statistics-bottom-card{
        position: relative;
        max-width: 60%; /* changed */
        max-height: 20vw; /* changed */

        background-color: #FFFF;
        border-radius: 5px;
        padding: 70px 15px 35px;
        
        display: flex;
        flex-direction: column;
        align-items: center; /* new */
        justify-content: space-around;   
    }  
    
    .shortly-statistics-bottom-card:nth-child(3){ top: 0; /* changed */ }
    .shortly-statistics-bottom-card:nth-child(4){ top: 0; /* changed */ }

    .shortly-statistics-bottom-card h3{ padding-bottom: 20px; }
    .shortly-statistics-bottom-card p{ 
        text-align: center; /* new */
        color: #A09FA4; 
    }

    .shortly-statistics-card-image{
        position: absolute;
        top: -35px;
        left: 50%; /* changed */
        transform: translateX(-50%); /* new */
    
        background-color: #000;
        padding: 10px;
        border-radius: 50%;
    }

    .shortly-statistics-bottom-hr{
        background-color: aqua;
        position: absolute;
        rotate: 90deg;
        top: 41%;
        min-height: 6px; /* changed */
        min-width: 100%;
    }

    /* --------------------------------------------------  Footer */
    .shortly-footer{ 
        position: relative;
  
        max-width: 100%; /* new */
        margin-top: 60px; /* new*/
    }

    .shortly-footer-top{
        background-color: #3A3053;
        background-image: url('../../components/Navigate/Shortly/images/bg-boost-mobile.svg'); /* changed*/
        background-repeat: no-repeat;
        background-size: cover;

        max-height: 300px; /* new */
        max-width: 100%; /* new*/

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
        /* margin top 50px ? */
    }

    .shortly-footer-top button{
        background-color: #29D2CF;
        min-width: 200px; /* changed */
        max-height: 54px; /* changed */

        font-size: larger;
        font-weight: 900;
        border-radius: 30px;
    }
    
    .shortly-footer-top h2{
        color: #FFFF;
        font-size: 28px; /* changed */
        text-align: center; /* new */
    }
    
    .shortly-footer-bottom{
        background-color: #232027;
        color:#FFFF;

        display: flex;
        flex-direction: column; /* changed */
        justify-content: space-around;
        align-items: center; /* new */
        gap: 40px;

        max-width: 100%; /* new */
        min-height: 210px;
        padding-top: 4.5rem;
     
    }

    :is(.shortly-facebook, .shortly-instagram, .shortly-twitter, .shortly-pinterest){ fill: #FFF; }
    :is(.shortly-facebook, .shortly-instagram, .shortly-twitter, .shortly-pinterest):hover{ fill: #29D2CF; }

    .shortly-footer-center{
        display: flex;
        flex-direction: column; /* new */
        justify-content: space-between;
        align-items: center; /* changed */
        gap: 40px;

        min-width: 50%; /* changed */
        max-width: 50%; /* new */
    }

    .shortly-footer-center h4{ 
        text-align: center; /* new */
        margin-bottom: 27px; }
    
    .shortly-footer-center p{ 
        text-align: center; /* new */
        margin-bottom: 1rem; }

    .shortly-footer-right{
        min-width: 50%; /* changed */
        min-height: 36px;

        display: flex;
        justify-content: space-around; /* changed*/
        align-items: center;
        
        padding-bottom: 50px; /* new */
    }
    
    :is(.shortly-footer-bottom) p:hover{
        color: #29D2CF;
        cursor: pointer;
    }
}