@media screen and (max-width: 428px) {

.jobfilter-wrapper *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-align: start;
}
.jobfilter-topBG{
    min-width: 100%;
    min-height: 155px;
    background-color: #5DA5A4;
    background-image: url("../../components/Navigate/JobFilter/images/bg-header-mobile.svg"); /* changed */
    position: absolute;

}
/* All job options component */
.jobfilter-all-options-wrapper{
    border: 1px solid blue;
    background-color: #F0FAFB;
    padding-bottom: 100px;
    
   
}

/* Filtred Jobs */
.jobfiltred-wrapper{
    background-color: #F0FAFB;
    padding-bottom: 100px;
    min-height: 91.5vh;
}

/* Search (select ) bar */
.jobfiltred-skill-selected{
    background-color: #FFF;
    border-radius: 5px;
    box-shadow: 0 10px 15px #5fa3a454;

    max-width: 80%;
    min-width: 0px; /* changed */
   
    position: relative;
    left: 50%;
    top: 120px;
    transform: translateX(-50%);
   

    min-height: 125px; /* changed*/
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    padding: 0 40px;
    margin-bottom: 175px;
   
    
}


.jobfiltred-skill-selected-left{
    display: flex;
    gap: 16px;
}

.jobfiltred-skill-selected-left select{
    border: none;
    padding: 5px;
    font-weight: 900;
    color: #5FA3A4;
    background-color: #F0F5F3;
    border-radius: 5px;
    font-size: 11px; /* new */
    margin: 0 10px 10px 0; /* new */
    
    
}
.jobfiltred-skill-selected-left select:last-child{ /* new */
    margin: 0;/* new */
}
.jobfiltred-skill-selected-left select:hover{
    color: black;
}
.jobfiltred-skill-selected-left select,
.jobfiltred-skill-selected-right{
    color: #5FA3A4;
    font-weight: 900;
    cursor: pointer;
}

.jobfiltred-skill-selected-right{ /* new */
    color: #808A89;  /* new */
}

/* The job card */
.jobfilter-card,
.jobfilter-card-clicked{
    max-width: 80%;
    min-width: 0px; /* changed */
    min-height: 260px; /* changed */
    padding: 0 25px 5px; /* changed */
    background-color: #FFF;
    position: relative;
    left: 50%;
    transform: translateX(-50%);

    display: flex;
    flex-direction: column; /* new */
    justify-content: space-around; /* changed */
    align-items: flex-start; /* changed */
     margin-bottom: 40px; /* changed */

    border-radius: 5px;
    border-left: 5px solid transparent;
    box-shadow: 0 10px 15px #5fa3a454;
}


.jobfilter-card-clicked{
    border-left: 5px solid #5FA6A4;
}

/* Card section Left */
.jobfilter-card-section-left{
    display: flex;
    flex-direction: column; /* new */
    align-items: flex-start; /* changed */
    
    gap: 23px;
    min-width: 35%;
    max-height: 90px;

    position: relative; /* new */
    top: -50px; /* new */ 
}


/* Please learn how to use images from data.json */
.logo-photosnap,
.logo-manage,
.logo-account,
.logo-myhome,
.logo-loop-studios,
.logo-faceit,
.logo-shortly,
.logo-insure,
.logo-eyecam-co,
.logo-the-air-filter-company{
    min-width: 50px; /* changed */
    min-height: 50px; /* changed */
    border-radius: 50%;
    
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
}

.logo-photosnap{background-image: url('../../components/Navigate/JobFilter/images/photosnap.svg');}
.logo-manage{background-image: url('../../components/Navigate/JobFilter/images/manage.svg');}
.logo-account{background-image: url('../../components/Navigate/JobFilter/images/account.svg');}
.logo-myhome{background-image: url('../../components/Navigate/JobFilter/images/myhome.svg');}
.logo-loop-studios{background-image: url('../../components/Navigate/JobFilter/images/loop-studios.svg');}
.logo-faceit{background-image: url('../../components/Navigate/JobFilter/images/faceit.svg');}
.logo-shortly{background-image: url('../../components/Navigate/JobFilter/images/shortly.svg');}
.logo-insure{background-image: url('../../components/Navigate/JobFilter/images/insure.svg');}
.logo-eyecam-co{background-image: url('../../components/Navigate/JobFilter/images/eyecam-co.svg');}
.logo-the-air-filter-company{background-image: url('../../components/Navigate/JobFilter/images/the-air-filter-company.svg');}

.jobfilter-card-context{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.jobfilter-card-context-top{
    display: flex;
    justify-content: flex-start;
    gap: 8px;
}
.jobfilter-card-context-top small{
    color: #5FA6A4;
    margin-right: 17px; /* changed */
    font-weight: 900; /* changed */
   
}

.jobfilter-card-new{
    background-color: #60A0A2; /* changed */
}
.jobfilter-card-featured{background-color: #2C3836;}

.jobfilter-card-new,
.jobfilter-card-featured{
    font-size: 11px;
    font-weight: 700;
    border-radius: 20px;
    padding: 2px 7px;
    color: #FFF;
}

.jobfilter-card-context-center{
    font-weight: 900;
    font-size: 14px; /* changed */
    margin-top: 10px;
    margin-bottom: 16px;
}

.jobfilter-card-context-bottom{
    display: flex;
    justify-content: space-between;
    font-weight: 600; /* changed */
    font-size: 11px; /* changed */
    padding-bottom: 10px;
    border-bottom: 1px solid #909496; 
    color: #909496;  
}

.jobfilter-card-context-bottom ul{ display: flex; }

.jobfilter-card-context-bottom li{ margin-left: 42px; }

/* Card section Right */
.jobfilter-card-section-right{
    display: flex;
    flex-wrap: wrap; /* new */
    justify-content:flex-start; /* changed */
    gap: 16px;
    max-width: 80%; /* new */

    position: relative; /* new */
    top: 12px; /* new */  
}
.jobfilter-card-section-right button{
    background-color: #cef5db; /* changed */
    color: #5FA3A4;
    cursor: pointer;
    font-weight: 900;
    padding: 5px;
    border-radius: 5px;
    border: none;
    font-size: 11px; /* new */
    max-width: fit-content; /* new */
    max-height: 30px; /* new */
}
.jobfilter-card-section-right button:hover{
    background-color: #5FA3A4;
    color: #FFF;
}
}